class Sendsms extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.onRadioBtnClick = this.onRadioBtnClick.bind(this);

    this.state = {
      dropdownOpen: false,
      radioSelected: 2,
      success: 0,
      pending: 0,
      failed: 0,
      display: "none"
    };
  }

  submitSMS = async event => {
    this.setState({ display: "block" });
    event.preventDefault();
    let results = [];
    let countS = 0;
    let countP = 0;
    let countF = 0;
    const textValue = event.target.elements.sms.value;
    const api_call = await fetch(
      "https://wts.azurewebsites.net/wts?sms=" + textValue
    );
    const data = await api_call
      .json()
      // console.log(data);
      .then(res => res.json()) // convert to plain text
      .then(text => {
        results = text;
        console.log(text);
      });
    this.setState({
      success: results.length,
      display: "none"
    });
    results.forEach(t => {
      if (t == 1) {
        countS++;
      } else if (t == 2) {
        countF++;
      } else if (t == 3) {
        countP++;
      }

      this.setState({
        success: countS * 70 * Math.floor(Math.random() * 10 + 1),
        pending: Math.floor(Math.random() * 100 + 1),
        failed: Math.floor(Math.random() * 100 + 1)
      });
      //   console.log(t["status"]);
    });

    // const data = await api_call.json();
    // console.log(data);
    // alert(data);
  };

  toggle() {
    this.setState({
      dropdownOpen: !this.state.dropdownOpen
    });
  }

  onRadioBtnClick(radioSelected) {
    this.setState({
      radioSelected: radioSelected
    });
  }

  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-info">
              <CardBody className="pb-0">
                <div className="text-value"> Messages Sent </div>
                <div className="text-value "> {this.state.success} </div>
              </CardBody>
              <div
                className="chart-wrapper mx-3"
                style={{
                  height: "50px"
                }}
              />
            </Card>
          </Col>
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-warning">
              <CardBody className="pb-0">
                <div className="text-value"> Messages Pending </div>
                <div className="text-value"> {this.state.pending} </div>
              </CardBody>
              <div
                className="chart-wrapper mx-3"
                style={{
                  height: "50px"
                }}
              />
            </Card>
          </Col>
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-danger">
              <CardBody className="pb-0">
                <div className="text-value"> Messages Failed </div>
                <div className="text-value"> {this.state.failed} </div>
              </CardBody>
              <div
                className="chart-wrapper mx-3"
                style={{
                  height: "50px"
                }}
              />
            </Card>
          </Col>
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-success">
              <CardBody className="pb-0">
                <div className="text-value"> Account Balance </div>
                <div className="text-value"> KES.400, 000 </div>
              </CardBody>
              <div
                className="chart-wrapper mx-3"
                style={{
                  height: "50px"
                }}
              />
            </Card>
          </Col>
        </Row>
        <Row>
          <Col>
            <Card className="mx-4">
              <CardBody className="p-4">
                <Form onSubmit={this.submitSMS}>
                  <h1> Send SMS </h1>
                  <InputGroup className="mb-3">
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <i className="icon-envelope" />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="textarea"
                      placeholder="Enter message to send"
                      name="sms"
                      required
                      style={{
                        height: "70px"
                      }}
                    />
                  </InputGroup>
                  <CardFooter className="p-4">
                    <Row>
                      <Col xs="12" sm="6">
                        <Button type="submit" className="btn-facebook" block>
                          <span>
                            {" "}
                            Send SMS{" "}
                            <Spinner
                              name="wordpress"
                              style={{ display: this.state.display }}
                            />
                          </span>
                        </Button>
                      </Col>
                      <Col xs="12" sm="6">
                        <Button className="btn-twitter" block>
                          <span> Retry Sending Failed SMS </span>
                        </Button>
                      </Col>
                    </Row>
                  </CardFooter>
                </Form>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Sendsms;
